<template>
    <el-dialog v-dialogDrag :title="dialogTitle" :visible.sync="visible" @close="closeDialog(false)">
        <el-form size="mini" :inline="true">
            <el-form-item label="">
                <el-input v-model="dialogData.userName" placeholder="用户名" :maxlength="60" class="inputwinth192">
                    <template slot="prepend">用户名</template>
                </el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.nickName" placeholder="真实姓名" :maxlength="60" class="inputwinth192">
                    <template slot="prepend">真实姓名</template>
                </el-input>
            </el-form-item>
            <el-form-item label="性别" label-width="110px">
                <el-radio v-model="dialogData.is_Male" :label="1">男</el-radio>
                <el-radio v-model="dialogData.is_Male" :label="0">女</el-radio>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.phoneNum" placeholder="手机号码" :maxlength="20" class="inputwinth192">
                    <template slot="prepend">手机号码</template>
                </el-input>
            </el-form-item>
            <!-- <el-form-item label="角色" label-width="41.6px">
                <el-select v-model="dialogData.roleId" placeholder="角色" :maxlength="20" class="inputwinth150">
                    <el-option v-for="item in roleList" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="部门" label-width="41.6px">
                <el-select v-model="dialogData.organizeId" placeholder="部门" :maxlength="20" class="inputwinth150">
                    <el-option v-for="item in organizeList" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item> -->
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" icon="fa fa-file-o" @click="closeDialog(false)"> 取 消</el-button>
            <el-button size="mini" icon="fa fa-file-text-o" type="primary" @click="saveDialog"> 保 存</el-button>
        </div>
    </el-dialog>
</template>
 
<script>
export default {
    data() {
        return {
            moduleName: "userInfo",
            visible: this.dialogShow,
            roleList: [],
            organizeList: []
        };
    },
    props: {
        dialogTitle: "",
        dialogShow: false,
        dialogData: {
            id: 0,
            code: "",
            name: "",
            is_Enable: 1,
            is_Remove: 0
        }
    },
    created() {
        this.initData();
    },
    methods: {
        initData() {
            // this.$ajax.get("role/findAll").then(res => {
            //     this.roleList = res.data;
            // });
            // this.$ajax.get("role/findAll").then(res => {
            //     this.organizeList = res.data;
            // });
        },
        closeDialog(isclose) {
            this.$emit("update:dialogShow", isclose);
        },
        saveDialog() {
            var url =
                this.moduleName +
                (this.dialogData.id > 0 ? "/updateOne" : "/insertOne");
            this.$ajax.post(url, this.dialogData).then(res => {
                this.$emit("update:dialogData", res.data);
                this.$emit("update:dialogShow", false);
                this.$emit("RefreshData");
            });
        }
    },
    watch: {
        dialogShow() {
            this.visible = this.dialogShow;
        }
    }
};
</script>



